<template>
  <div>
    <nav-head title="金额明细" FixedOrTransparency="isFixed NoTransparency"/>
    <!--<ul class="thead"><li>时间</li><li>事件内容</li><li>金额变动</li></ul>-->
    <Loadmore :top-method="loadTop" ref="loadmore" >
      <div
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
        infinite-scroll-immediate-check="false"
      >
        <div class="tbody">
          <div v-for="item in MoneyLog" v-bind:key="item.id">
            <span class="time">{{item.create_time | dateFormat('yyyy-MM-dd hh:mm:ss')}}</span>
            <span class="description">{{item.description}}</span>
            <span class="value" v-bind:class="{ 'color-green': item.diamond.indexOf('-') >= 0 }">{{item.diamond.indexOf('-') >= 0 ? item.diamond : "+"+item.diamond}}</span>
          </div>
        </div>
        <div v-show="loading!==1" style="color: #999;text-align: center;line-height: 35px;">
          加载中•••
        </div>
      </div>
    </Loadmore>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import { mapActions, mapState } from 'vuex'
import { GET_MONET_LOG } from '../../../vuex/Mine'
import Loadmore from '../../../components/loadmore'

export default {
  name: 'moneyLog',
  data: function () {
    return {
      loading: null,
      type: 2,
      topStatus: ''
    }
  },
  computed: {
    ...mapState({
      MoneyLog: state => state.mine.MoneyLog
    })
  },
  methods: {
    loadTop () {
      // 刷新数据
      this.getMoneyLog({
        type: this.type,
        index: 1,
        func: () => {
          setTimeout(() => {
            this.$refs.loadmore.onTopLoaded()
            if (this.loading) this.loading = false
            if (this.MoneyLog.length >= 10) {
              this.loadMore()
            } else {
              this.loading = 1
            }
          }, 500)
        }
      })
    },
    loadMore () {
      // 加载更多数据
      this.loading = true
      if (this.MoneyLog.length > 0 && this.MoneyLog.length % 10 !== 0) return
      const leng = this.MoneyLog.length
      this.getMoneyLog({
        type: this.type,
        index: 2,
        func: () => {
          if (leng !== this.MoneyLog.length && this.MoneyLog.length % 10 === 0) {
            // 长度变化且能被整除
            this.loading = false
          } else {
            // 长度没变拿不到新数据了,永远禁止上拉加载
            this.loading = 1
          }
        }
      })
    },
    ...mapActions({
      getMoneyLog: GET_MONET_LOG
    })
  },
  created () {
    this.getMoneyLog({
      type: 2,
      index: 1,
      func: () => {
        if (this.MoneyLog.length >= 10) {
          this.loadMore()
        } else {
          this.loading = 1
        }
      }
    })
  },
  components: {
    Loadmore,
    NavHead
  }
}
</script>
